<template>
	<div>
		<div class="parent">
			<div class="timer">{{ hour }}</div>
			<span class="double-point">:</span>
			<div class="timer">{{ minute}}</div>
			<span class="double-point">:</span>
			<div class="timer">{{ second }}</div>
		</div>
	</div>
</template>

<script>
    export default {
        name: "Timer",

        data() {
            return {
                hour: 23,
                minute: 59,
                second: 59,
            }
        },


        mounted() {
            const vm = this;
            setInterval(function () {
                if (vm.second > 0) {
                    vm.second--;
                } else if (vm.second == 0) {
                    vm.second = 59;
                    if (vm.minute > 0) {
                        vm.minute--;
                    } else if (vm.minute == 0) {
                        vm.minute = 59;
                        if (vm.hour > 0) {
                            vm.hour--;
                        } else if (vm.hour == 0) {
                            vm.hour = 23;
                        }
                    }
                }
            }, 1000)
        }

    }
</script>

<style scoped lang="scss">
	.parent {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 20%;


		.timer {
			background-color: #222;
			color: #ffffff;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 1rem;
			border-radius: .25rem;
			width: 40px;
			height: 32px;
		}

		.double-point {
			display: flex;
			justify-content: center;
			align-items: center;
			padding: .12rem;
			font-weight: bold;
			font-size: 20px;
		}
	}
</style>