<template>
	<div id="steper">
		<br>
		<div class="boxes">
			<div class="box" v-for="i in rows" :key="i">
				<div class="arc-right" v-if="(i < rows)&&(i % 2 == 0)&&(anyItems[i])" :style="{backgroundImage: `url(./icons/end-arrow.svg)`}">
				</div>
				<div class="arc-left" v-if="(i < rows)&&(i % 2 != 0)&&(anyItems[i])"  :style="{backgroundImage: `url(./icons/end-arrow.svg)`}">
				</div>

				<div class="items" :class="{'row-rev' : i % 2 == 0}" v-if="anyItems.length > 0">
					<div class="item" :class="{active : item.step }" v-for="(item,index) of anyItems[i-1]" :key="index">
						<div class="icon" :style="{backgroundImage : `url(icons/${item.icon})`}"></div>
						<br>
						<div class="point" :class="{'active-point' : item.step}">
							<div class="line-left" v-if="anyItems[i] && i % 2 == 0 && index < column-1"></div>
							<div class="line-right" v-if="anyItems[i] && i % 2 != 0 && index < column-1"></div>
						</div>
						<div class="title">{{ item.title }}</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
    export default {
        name: "SpiralSteper",

        props: {
            items: Array,
            step: Number,
            column: Number
        },


        data() {
            return {
                anyItems: [],
                rows: null,
            }
        },

        methods: {
            addCounter() {
                this.counter = this.counter + 1;
            },

            copy(array, position = 0, length = 1, step = 1) {
                let arr = [];
                let counter = 0;
                for (let i = position; i < position + length; i++) {
                    if (array[i]) {
                        arr[counter] = array[i];
                        if (i < step) {
                            arr[counter].step = true;
                        }
                        console.log(arr[counter]);
                    }
                    counter++;
                }
                return arr;
            },

            breakArray(array, section = 1) {
                let position = 0;
                let length = section;
                let arr = [];
                let i = 0;
                let j = 0;
                while (position < array.length) {
                    arr[i, j] = this.copy(array, position, length, this.step);
                    i++;
                    j++;
                    position += length;
                }
                return arr;
            },


        },

        mounted() {
            this.anyItems = this.breakArray(this.items, this.column);
            this.rows = Math.floor(this.items.length / this.column);
            if (this.items % this.column != 0) {
                this.rows++;
            }
        }
    }
</script>

<style scoped lang="scss">
	@import "src/assets/scss/valus.scss";

	#steper {
		z-index: -1;

		.boxes {
			display: flex;
			flex-direction: column;
			width: 100%;


			.box {
				position: relative;
				width: 100%;
				height: 90px;
				margin-bottom: 5px;

				.items {
					display: flex;
					justify-content: flex-start;
					align-items: center;
					padding: $global-padding;

					&.row-rev {
						flex-direction: row-reverse;
					}

					.item {
						width: calc(33.33% - 10px);
						margin: 5px;
						text-align: center;
						background-color: #ffffff;
						border-radius: $global-border-radius;
						padding: 10px;

						&.active{
							background-color: #8effc5;
						}

						.icon {
							width: 20px;
							height: 20px;
							display: inline-block;
							background-position: center;
							background-size: cover;

						}


						.point {
							width: 10px;
							height: 10px;
							border-radius: 50%;
							background-color: #999999;
							display: inline-block;
							position: relative;

							&.active-point{
								background-color: green;
							}

							.line-left {
								width: 45px;
								height: 2px;
								position: absolute;
								background-color: #000000;
								top: 5px;
								left: 22px;

								&:after{
									content: '';
									width: 5px;
									height: 5px;
									border: 3px solid #000000;
									transform: rotate(45deg);
									position: absolute;
									right: 0;
									top : -3px;
									border-left: none;
									border-bottom: none;
								}
							}

							.line-right {
								width: 45px;
								height: 2px;
								position: absolute;
								background-color: #000000;
								top: 5px;
								right: 22px;

								&:after{
									content: '';
									width: 5px;
									height: 5px;
									border: 3px solid #000000;
									transform: rotate(45deg);
									position: absolute;
									left: 0;
									top : -3px;
									border-right: none;
									border-top: none;
								}
							}
						}

						.title {
							font-size: .6rem;
							white-space: nowrap;
						}
					}
				}

				.arc-right {
					top: 65%;
					right: -5px;
					position: absolute;
					width: 60px;
					height: 140%;
					z-index: 1;
					background-size: cover;
					background-position: center;
					transform: rotateY(180deg);

				}

				.arc-left {
					top: 65%;
					left: -5px;
					position: absolute;
					width: 60px;
					height: 140%;
					z-index: 1;
					background-size: cover;
					background-position: center;
				}

			}
		}
	}

</style>
