										<accordion :current-route="$router.currentRoute.name" :title="item.title" :icon="item.icon" :options="item.array" :show="isShowListAccordion"/>
									</div>

								</div>
							</div>
						</div>


						<spiral-steper :items="steperItems" :column="3" :step="5"/>


