<script>
    // import HelpUser from "./HelpUser";
    import GetMultiImage from "./GetMultiImage";
    import Accordion from "./Accordion";
    // import Steper from "./Steper";
    // import ColumnSteper from "./ColumnSteper";
    import SpiralSteper from "./SpiralSteper";


    export default {
        name: "CustomChatbot",

        components: {
            GetMultiImage,
            Accordion,
            // HelpUser,
            // ColumnSteper
            // Steper,
            SpiralSteper
        },

        props: {
            items: Array,
        },


        data() {
            return {
                showCloseBalloon: false,
                showItemsBalloon: false,
                showLayer: false,
                showChatBot: false,
                showMinimizeBalloon: false,
                showWarning: false,
                showCloseBox: false,
                isOpenImageBox: true,
                isCollection: false,
                isMaximizeBoxImage: false,
                isShowListAccordion: false,
                colorSend: '#999',
                messages: [],
                images: [],
                urls: [],
                st: '',
                typeUser: false,
                showBoxImages: false,
                isAdd: false,
                arraySlider: [
                    {text: 'اینجا پشتیبانی هوشمند باسلامه ', image: 'support.svg', id: 0},
                    {text: 'لیست سفارش هاتو داری؟', image: 'note.svg', id: 1},
                    {text: 'و میتونی هر کاری خواستی باهاشون بکنی!', image: 'poem.svg', id: 2},
                    {text: 'کنسلی اعلام مشکل ثبت رضایت و ...', image: 'businessman.svg', id: 3},
                    {text: 'ولی نگران نباش...', image: 'worried.svg', id: 4},
                    {text: 'اگر کارت راه نیافتاد کارشناس درجا بهت جواب میده', image: 'full_support.svg', id: 5},
                ],

                steperItems: [
                    {title: 'مرحله اول', icon: 'accepted.svg', step: false},
                    {title: 'مرحله دوم', icon: 'gear.svg', step: false},
                    {title: 'مرحله سوم', icon: 'tracking.svg', step: false},
                    {title: 'مرحله چهارم', icon: 'delivery.svg', step: false},
                    {title: 'مرحله پنجم', icon: 'tick.svg', step: false},
                    {title: 'مرحله ششم', icon: 'tick.svg', step: false},
                    {title: 'مرحله هفتم', icon: 'tick.svg', step: false},
                    {title: 'مرحله هشتم', icon: 'tick.svg', step: false},
                    {title: 'مرحله نهم', icon: 'tick.svg', step: false},
                    // {title: 'مرحله دهم', icon: 'tick.svg', step: false},
                    // {title: 'مرحله یازدهم', icon: 'tick.svg', step: false},
                    // {title: 'مرحله دوازدهم', icon: 'tick.svg', step: false},
                    // {title: 'مرحله سیزدهم', icon: 'tick.svg', step: false},
                    // {title: 'مرحله چهردهم', icon: 'tick.svg', step: false},
                    // {title: 'مرحله 15', icon: 'tick.svg', step: false},
                    // {title: 'مرحله 16', icon: 'tick.svg', step: false},
                    // {title: 'مرحل 17', icon: 'tick.svg', step: false},
                ],
            }
        },


        methods: {

            openAccordion() {
                const vm = this;
                let h = vm.$refs.calcHeight.clientHeight;
                window.console.log(h);
            },

            scrollBottom() {
                const vm = this;
                this.$nextTick(() => {
                    let content = vm.$refs.content;
                    content.scrollTop = content.scrollHeight;
                    vm.isAdd = false;
                    window.console.log(vm.messages);
                })
            },

            openOrCloseImageBox() {
                const vm = this;
                let accordion = vm.$refs.accordion;
                let collection = vm.$refs.collection;
                if (vm.isOpenImageBox) {
                    accordion.style.transform = 'rotate(180deg)';
                    collection.style.top = '90%';
                    vm.isMaximizeBoxImage = false;

                } else {
                    accordion.style.transform = 'rotate(0)';
                    collection.style.top = '0';
                    vm.isMaximizeBoxImage = true;
                }
                vm.isOpenImageBox = !vm.isOpenImageBox;
            },


            addMessage() {
                const vm = this;
                let ob = {};
                if (vm.st.length > 0) {
                    vm.isAdd = true;
                    ob.text = vm.st;
                    let dd = new Date();
                    let hh = dd.getHours();
                    let mm = dd.getMinutes();

                    // let ss = dd.getSeconds();
                    ob.images = [];
                    ob.time = hh + ':' + mm;
                    ob.urls = [];
                    this.messages.push(ob);
                    vm.colorSend = '#999';
                }
                vm.st = '';
                vm.typeUser = false;
            },

            addImages() {
                const vm = this;
                if (vm.images.length > 0) {
                    vm.isMaximizeBoxImage = false;
                    vm.isAdd = true;
                    vm.showBoxImages = false;
                    vm.isCollection = false;
                    let ob = {};
                    ob.images = [];
                    ob.urls = [];
                    ob.images.push(...vm.images);
                    for (let i = 0; i < ob.images.length; i++) {
                        ob.urls.push(vm.createObjectUrl(ob.images[i]));
                    }
                    let dd = new Date();
                    let hh = dd.getHours();
                    let mm = dd.getMinutes();
                    // let ss = dd.getSeconds();
                    ob.time = hh + ':' + mm;
                    ob.text = '';
                    vm.messages.push(ob);
                    vm.st = '';
                    vm.images = [];
                    vm.urls.push(vm.createObjectUrl(vm.images[0]));
                    vm.typeUser = false;
                }
            },

            fullImages(e) {
                const vm = this;
                vm.showBoxImages = true;
                vm.images.push(...e.target.files);
                vm.isCollection = true;
                vm.isMaximizeBoxImage = true;

            },

            closeImageBox() {
                const vm = this;
                vm.showBoxImages = false;
                vm.isCollection = false;
            },

            createObjectUrl(imageFile) {
                // return window.URL.createObjectURL(imageFile);
                return (window.URL) ? window.URL.createObjectURL(imageFile) : window.webkitURL.createObjectURL(imageFile);
            },

            deleteImage(index) {
                const vm = this;
                vm.images.splice(index, 1);
                vm.urls.splice(index, 1);
            },


            activeLayer() {
                const vm = this;
                vm.showLayer = true;
                vm.showCloseBox = true;
            },

            closeChatBot(type) {
                const vm = this;
                vm.showMinimizeBalloon = false;
                vm.showCloseBalloon = false;
                vm.showLayer = false;
                vm.showChatBot = false;
                if (type == 'close') {
                    vm.showWarning = true;
                }
            },


            type(e) {
                if (e.key != `Enter`) {
                    const vm = this;
                    vm.colorSend = 'rgb(220, 55, 85)';
                    vm.typeUser = true;
                }
            }

        },


    }
</script>

<style scoped lang="scss">
