		}

		.menu {

			.calc-height {

				.item {
					width: 100%;
					height: 40px;
					padding: $global-padding/2 $global-padding;
					background-color: pink;
					border-radius: $global-border-radius;
					cursor: pointer;
					/*overflow: hidden;*/

					.array {
						border-radius: $global-border-radius;
						white-space: nowrap;
						background-color: green;
						color: #ffffff;
						padding: 5px;
						width: 100%;
						font-size: .8rem;
						margin: 5px;

					}

				}

				> .title {
					display: flex;
					align-items: center;
					justify-content: flex-start;
					font-size: .9rem;
					font-weight: bold;

					.text {
						font-size: $global-font-text;

					}

					.icon {
						width: 60%;
						height: 60%;
						background-position: center;
						background-size: cover;
						transition: opacity .5s;

					}
				}

			}
		}

		.balloon {
			background-color: #000000;
			color: #ffffff;
			padding: $global-padding;
			border-radius: $global-border-radius;
			position: absolute;
			bottom: -60px;
			z-index: 2;


			.arrow {
				content: '';
				position: absolute;
				width: .5rem;
				height: .5rem;
				background-color: #000000;
				transform: rotate(45deg);
				top: -.25rem;
			}

			.text {
				font-size: $global-font-text;
				white-space: nowrap;
			}
		}

		.close {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			height: 100%;
			border-radius: 50%;
			cursor: pointer;
			transition: all .5s;
			position: relative;

			svg {
				transition: all .5s;
			}

			&:hover {
				background-color: rgba(255, 0, 0, 0.7);;

				svg {
					fill: #ffffff;
				}
			}
		}

		.bot-btn {
			width: 100%;
			padding: $global-padding/2 $global-padding;
			background-color: $global-color;
			color: #ffffff;
			border-radius: $global-border-radius;
			cursor: pointer;
			text-align: center;
		}

		/*** fade transition ***/

		.fade-enter {
			opacity: 0;
			/*transform: translateY(10%) scale(.999);*/
		}

		.fade-leave-active,
		.fade-enter-active {
			transition: all .5s ease-out;
		}

		.fade-leave-to {
			opacity: 0;
			/*transform: translateY(10%) scale(.999);*/
		}


		/*** top transition ***/
		.top-enter {
			transform: translate(100%, 100%) scale(0);
		}

		.top-leave-active,
		.top-enter-active {
			transition: all .5s ease-out;
		}

		.top-leave-to {
			transform: translate(50%, 50%) scale(0);
			opacity: 0;
		}

		/*** bottom ***/
		.bottom-enter {
			transform: translateY(100%);
		}

		.bottom-leave-active,
		.bottom-enter-active {
			transition: all .5s ease-out;
		}

		.bottom-leave-to {
			transform: translateY(100%);
			opacity: 0;
		}

		.bottom-move {
			transition: transform 5s ease-out;
		}


		/*** move ***/
		.move-enter {
			transform: translateY(100%) scale(0);
		}

		.move-enter-active,
		.move-leave-active {
			transition: all .5s ease-out;
		}

		.move-leave-to {
			transform: translateY(100%) scale(0);
		}


		.opacity-off {
			opacity: 0;
			display: none;
		}

		.scrollNone {
			overflow: hidden;
		}


	}

	@keyframes a {
		0% {
			transform: scale(0)
		}
		50% {
			transform: scale(.5)
		}
		100% {
			transform: scale(1)
		}
	}

	@keyframes b {
		0% {
			transform: translateY(15%)
		}
		100% {
			transform: translateY(0)
		}
	}

