<template>
	<div id="accordion">
		<div class="accordion" @click="showHideAccordion">
			<div class="title">
				<div class="text-section">
					<div class="text">{{ title }}</div>
					<div class="icon" :style="{backgroundImage : `url(/icons/${icon})`}"></div>
				</div>
				<div class="arrow" ref="arrow">
					<div class="icon"></div>
				</div>
			</div>
			<div class="items" ref="itemsAccordion">
				<div class="calc-height" ref="calcHeight">
					<div :class="{'active' : currentRoute === option.path || index === myIndex}" class="item" v-for="(option,index) of optionsSort" :key="index" @click="goToRoute(option,index)">
						<div>{{ option.name }}</div>
						<div v-if="option.price">{{ option.price.toLocaleString('fa') }} <span>تومان  </span></div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
    export default {
        name: "Accordion",

        props: {
            options: Array,
            title: String,
            icon: String,
            show: Boolean,
            currentRoute: String,
            // value: Number,
        },

        data() {
            return {
                myIndex: null,
                optionsSort: [],
            }
        },

        methods: {
            goToRoute(option, index) {
                const vm = this;
                if (option.id > -1) {
                    vm.isAccordion = false;
                    vm.myIndex = index;
                    vm.$emit('showChange', false);
                    vm.$emit('input', option.id)
                } else if (option.path === '' || option.path === null || option.path === 'undefined') {
                    alert('مسیر داده شده خالی است');
                } else {
                    if (option.path === 'index' && (vm.$store.state.cityIdInStore > 0)) {
                        vm.$router.push({name: option.path, query: {city: vm.$store.state.cityIdInStore}});
                        vm.$emit('showChange', false);
                        vm.isAccordion = false;
                    } else {
                        vm.$router.push({name: option.path});
                        vm.$emit('showChange', false);
                        vm.isAccordion = false;
                    }
                }
            },


            showHideAccordion() {
                const vm = this;
                let arrow = vm.$refs.arrow;
                let h = vm.$refs.calcHeight.clientHeight;
                let accordion = vm.$refs.itemsAccordion;

                if (!vm.isAccordion) {
                    arrow.style.transform = 'rotate(180deg)';
                    accordion.style.overflow = 'hidden';
                    accordion.style.height = h + 'px';
                    vm.isAccordion = true;
                } else {
                    arrow.style.transform = 'rotate(0deg)';
                    accordion.style.overflow = 'hidden';
                    accordion.style.height = '0';
                    vm.isAccordion = false;
                }

            },

            sortAccordion(arr) {
                for (let i = 0; i < arr.length; i++) {
                    let k = 0;
                    for (let j = i + 1; j < arr.length; j++) {
                        if (arr[i].price < arr[j].price) {
                            k = j;
                        }
                        let t = arr[k];
                        arr[k] = arr[i];
                        arr[i] = t;
                    }
                }
            }
        },

        mounted() {
            const vm = this;
            vm.optionsSort = vm.options;
            vm.sortAccordion(vm.optionsSort);
        }

    }
</script>

<style scoped lang="scss">

	#accordion {

		.accordion {
			direction: rtl;
			text-align: right;
			padding: 1rem;
			cursor: pointer;


			> .title {
				display: flex;
				justify-content: space-between;
				align-items: center;
				background-color: pink;
				padding: .5rem;
				border-radius: .25rem;

				.text-section {
					display: flex;
					flex-direction: row-reverse;

					.text {
						font-size: .9rem;
						white-space: nowrap;
					}

					.icon {
						width: 1.5rem;
						height: 1.5rem;
						background-position: center;
						background-size: cover;
						margin-left: .5rem;
					}
				}

				.arrow {
					width: 1.5rem;
					height: 1.5rem;
					border-radius: 50%;
					display: flex;
					justify-content: center;
					align-items: center;
					transition: all .5s;

					.icon {
						width: .5rem;
						height: .5rem;
						border: 2px solid #62666d;
						border-radius: 2px;
						transform: rotate(45deg);
						border-left: none;
						border-top: none;
						transition: all .5s;
					}
				}
			}

			> .items {
				height: 0;
				width: 100%;
				overflow: hidden;
				transition: all .5s;
				background-color: #f5f5f5;
				margin-bottom: -1rem;
				z-index: -1;

				.item {
					margin: 10px 0;
					cursor: pointer;
					font-size: .9rem;
					line-height: .5rem;
					padding: .5rem;
					border-bottom: 1px solid #dedede;
					transition: all .2s;
					border-radius: .25rem;
					display: flex;
					justify-content: space-between;


					&:last-child {
						padding-bottom: 1.5rem;
						border: none;
					}

					&:hover {
						color: red;
					}
				}

				.active {
					color: red;
					font-weight: bold;
				}
			}
		}
	}
</style>
